.task{
    padding: 15px 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    border-bottom: 1px solid #FFD700;
    display: flex;
    align-items: center;
}
.task p{
    margin-left: 24px;
    color: rgba(255, 255, 255, 0.90);
    font-size: 20px;
    width: 100%;
    overflow-x: hidden;
    line-height: 1.6;
}
.task p.checked {
    text-decoration: line-through;
    color: rgba(255, 255, 255, 0.5);
}
.task__input{
    width: 20px;
    height: 20px;
}
.task__btn{
    margin-left: 24px;
    width: 30px;
    height: 30px;
    background-color: transparent;
}