*{
	padding: 0;
	margin: 0;
	border: 0;
}
*,*:before,*:after{
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,:active{outline: none;}


html,body{
	height: 100%;
	width: 100%;

}
input,button,textarea{font-family:inherit;}

button{cursor: pointer;}
ul li{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: 400;}
/*--------------------*/
body {
  background-color: #0D1117;
  
  min-height: 100%;
  font-family: Montserrat;
  font-weight: 400;
  line-height: 1.2;
}

#root{
  height: 100%;
}
.container{
  padding: 0 15px;
  min-height: 100%;
  max-width: 1030px;
  margin: 0 auto;
}

.wrapper{
  margin: 60px auto;
  padding: 0 15px;
  border-radius: 30px;
  padding: 30px;
  background: rgb(44,54,54);
  background: linear-gradient(172deg, rgba(70,100,104,0.8855917366946778) 39%, rgba(173,147,88,-1.2) 86%, rgba(76,120,90, .7) 135%);
}
.title{
  margin-bottom: 40px;
  color: rgba(255, 255, 255, 0.80);
  text-align: center;
  font-size: 45px;;
  font-weight: 700;
}