.form{
    display: flex;
    align-items: start;
    margin-bottom: 40px;
}
.form__input{
    border-radius: 3px;
    background: rgba(255, 255, 255, 0.60);
    padding: 12px 24px;
    width: 100%;
    font-size: 18px;
}

.form__input::placeholder{
    color: rgba(33, 33, 33, 1);
    font-weight: 300;
    font-size: 18px;
}
.form__input:focus{
    box-shadow: 6px 6px 6px 2px rgba(0, 0, 0, 0.2);
}
.form__btn{
    margin-left: 24px;
    padding: 9px 29px;
    border-radius: 3px;
    font-size: 18px;
    border-radius: 5px;
    border: 2px solid #FFD700;
    background: transparent;
    color: #FFD700;
    font-size: 20px;
    transition: all .3s ease;
}
.form__btn:hover{
    background: rgba(255, 215, 0, .2);
}
.form__btn:focus{
    box-shadow: 6px 6px 6px 2px rgba(0, 0, 0, 0.2);
}